body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    max-width:1180px ;
    margin: auto;
    padding-left:15px ;
    padding-right: 15px;
}
.homecont{
  background: linear-gradient(45deg, rgba(86, 58, 250, 0.9) 0%, rgba(116, 15, 214, 0.9) 100%);
  height: auto;

}
.homenav{
 
  height: auto;
}
.homenavbarflex{
 display: block;
}
.homenavlogo{
  padding-top: 50px;
}
.homenavlogo h2{
  font-size: 32px;
  color: #fff;
  text-decoration: none;
}
a{
  text-decoration: none;
}
.tasklistbtn button{
  padding: 8px 10px;
  margin-left: 15px;
  margin-top: 20px;
}
.tasklistbtn{
  margin-top: 50px;

}
.homebtn h3{
  font-size: 23px;
  color: #fff;
  padding-bottom: 10px;
  margin-right: 12%;
}
.homebtn button{
  padding: 8px 20px;
  margin-left: 5px;
}
.homemark h3{
  font-size: 23px;
  color: #fff;
  padding-top: 20px;
  padding-bottom: 10px;
  margin-right: 4%;
}
.homedate h3{
  font-size: 23px;
  color: #fff;
  padding-top: 20px;
  padding-bottom: 10px;
  margin-right: 17%;
}
.user{
  display: flex;
  
}
